// app/UsernameContext.tsx
"use client";
import { createContext, useContext, useState, useEffect, ReactNode } from "react";
import i18n from "../i18n"; // 引入 i18n 实例，用来同步语言

type Ctx = {
  username: string;
  setUsername: (u: string) => void;
  dark: boolean;
  toggleDark: () => void;
  font: string;
  setFont: (f: string) => void;
  zoom: string;
  setZoom: (z: string) => void;
};

const UsernameContext = createContext<Ctx>({
  username: "Admin",
  setUsername: () => {},
  dark: false,
  toggleDark: () => {},
  font: "16",
  setFont: () => {},
  zoom: "100",
  setZoom: () => {},
});

export const useUsername = () => useContext(UsernameContext);

export const UsernameProvider = ({ children }: { children: ReactNode }) => {
  const [username, setUsername] = useState("Admin");
  const [dark, setDark]   = useState(false);
  const [font, setFont]   = useState("16");
  const [zoom, setZoom]   = useState("100");

  /* 客户端挂载后统一读取 i18nextLng */
  useEffect(() => {
    setUsername(localStorage.getItem("username") || "Admin");
    setDark(localStorage.getItem("dark") === "true");
    setFont(localStorage.getItem("font") || "16");
    setZoom(localStorage.getItem("zoom") || "100");
  }, []);

  /* 任何改动持久化到 localStorage */
  useEffect(() => {
    localStorage.setItem("username", username);
    localStorage.setItem("dark", String(dark));
    localStorage.setItem("font", font);
    localStorage.setItem("zoom", zoom);
  }, [username, dark, font, zoom]);

  useEffect(() => {
    document.documentElement.classList.toggle("dark", dark);
    document.documentElement.style.setProperty("--user-font", `${font}px`);
    document.documentElement.style.setProperty("--user-zoom", `${zoom}%`);
  }, [dark, font, zoom]);

  return (
    <UsernameContext.Provider
      value={{
        username,
        setUsername,
        dark,
        toggleDark: () => setDark((d) => !d),
        font,
        setFont,
        zoom,
        setZoom,
      }}
    >
      {children}
    </UsernameContext.Provider>
  );
};